@use 'foundation' as *;

.l-inner {
  width: 100%;
  padding-right: $padding-sp;
  padding-left: $padding-sp;
  margin-right: auto;
  margin-left: auto;

  @include mq('md') {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
    max-width: $layout-width-inner + strip-unit($padding-pc) * 2;
  }
}
